export default {
	data() {
		return {
			datalist: [], // 分组列表
			empty:{},
			push:"push",
			change:"change",
			workinfo:{}
		}
	},
	methods: {
		// 获取分组
		getdatalist() {
			let that = this;
			that.$request.get("/plot/category/?projectId=" + this.workinfo.id ).then(res => {
					console.log(res, "请求的数据")
					that.datalist = res.data;
				})
				.catch(error => {
					console.error('error:', error);
				});
		},
		deletedata(data) {
			let that = this;
			uni.showModal({
			    title: '提示',
			    content: '确定删除此条数据吗',
			    success: function (res) {
			        if (res.confirm) {
						that.$request.delete("/plot/category/" + data.id +"/").then(res => {
								that.getdatalist();
							})
							.catch(error => {
								console.error('error:', error);
							});						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		operation(data,type){
			if(type ==="push"){
				uni.navigateTo({
					 url: './EditorDramaGrouping'
				})
			}else if(type ==="change"){
				let DramaGrouping=JSON.stringify(data);
				uni.navigateTo({
					 url: './EditorDramaGrouping?DramaGrouping='+DramaGrouping
				})
			}
		},
		gotodramaList(data){
			console.log("来一发")
			uni.navigateTo({
				 url: './dramaList/dramaList?categoryId='+data.id
			})
			
		},
	},
	mounted: function() {
		
	},
	onShow() {
		this.getdatalist();
		
	},
	onLoad() {
		let workinfo=uni.getStorageSync("workinfo");
		this.workinfo=workinfo;
	}
}
